<template>
  <div>
    <button @click="show = !show">toggleView</button>
    <p v-color="'red'">123123</p>
    <input type="text" id="fir" v-focus/>
    <!-- <div>
      <transition 
        @beforeEnter="beforeEnter"
        @enter="enter"
        @afterEnter="afterEnter"
        @beforeLeave="beforeLeave"
        @leave="leave"
        @afterLeave="afterLeave"
        :css="false"
        >
        <p class="amimate-p" v-show="show">i am a show</p>
      </transition>
    </div> -->
      
  </div>

</template>

<script>
import $ from 'jquery'
import TestA from './components/test'
import TestB from './components/test2'
import HelloWorld from './components/HelloWorld'
export default {
  components: {
    TestA,
    TestB,
    HelloWorld
  },
  data () {
    return {
      show: 1,
      currentView: 'TestA'
    }
  },
  directives: {
    color (dom, binding) {
      dom.style.color = binding.value
    },
    focus: {
      inserted (dom, binding) {
        console.log($('#fir').get(0))
        dom.focus()
      }
    }
  },
  methods: {
    beforeEnter (el) {
      console.log('beforeEnter')
      $(el).css({
        left: '-0px',
        opacity: 0
      })
    },
    enter (el, done) {
      console.log('enter')
      $(el).animate({
        left: '500px',
        opacity: 1
      }, {
        duration: 1500,
        complete: done
      })
    },
    afterEnter (el) {
      console.log('afterEnter')
    },
    beforeLeave (el) {
      console.log('beforeLeave')
    },
    leave (el, done) {
      console.log('leave')
      $(el).animate({
        left: '1000px',
        opacity: 0
      }, {
        duration: 1500,
        complete: done
      })
    },
    afterLeave (el) {
      console.log('afterLeave')
    }
  }
}
</script>

<style>

.fade-enter-active, .fade-leave-active {
  transition: all .5s ease-out;
}
.fade-enter {
  opacity: 0;
  transform: translateY(-500px)
}
.fade-leave-active {
  opacity: 0;
  transform: translateY(500px)
}
.amimate-p {
  position: absolute;
  top: 0;
  left: 500px;
}
html{
  height: 100%;
}
body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
</style>



