<template>
  <div>
    <slot name="header"></slot>
    {{ hello }} {{ numberTo }}
    <button @click="emitMyEvent">emitMyEvent</button>
  </div>
</template>

<script>
  export default {
    props: {
      numberTo: [Number]
    },
    data () {
      return {
        hello: 'I am a A'
      }
    },
    methods: {
      emitMyEvent () {
        this.$emit('myEvent', this.hello)
      }
    }
  }
</script>


